* {
    box-sizing: border-box;
}

.pages_wrapper div {
    display: inline-block;
    padding: .5rem;
    margin: .5rem;
    transition: all .2s ease;
}

.pages_wrapper div:hover {
    cursor: pointer;
    color: rgb(166, 195, 240);
}

.active_page{
    text-decoration: underline;
}


