.header {
  position: fixed;
  width: 100%;
  height: 9vh;
  color: white;
  grid-area: h;
  background-color: rgb(64, 102, 160);
  box-shadow: 0px 3px 6px 0px rgba(0,0,0,0.55);
}

/* Logo image */
.header img {
  position: absolute;
  padding-top: 1rem;
  left: 5rem;
  width: 5rem;
  height: auto;
}

.login {
  float: right;
  padding-top: 2rem;
  margin-right: 1.5rem;
  color: white;
  cursor: pointer;
}
